import { Component, OnInit, AfterViewChecked, OnDestroy } from '@angular/core';
import { LanguageService } from '../../language.service';
import { WikiService } from '../../wiki.service';
import { BrowserService } from '../../browser.service';
import { Location } from '@angular/common';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-loot-presents',
  templateUrl: './loot-presents.component.html',
  styleUrls: ['./loot-presents.component.css'],
  host: {'class': 'route-app'}
})

export class LootPresentsComponent implements OnInit, AfterViewChecked, OnDestroy {
  readonly ids = [764, 765, 766, 1149, 1160];
  readonly grades = ['good', 'great', 'best', 'legendary', 'rare'];
  id = 1160;
  tab = 4;
  stickyTop;
  navigationSubscription: Subscription;

  constructor(
    public lang: LanguageService,
    public wiki: WikiService,
    private browser: BrowserService,
    private location: Location,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.routeParse();
    this.navigationSubscription = this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        this.routeParse();
      }
    });
  }

  setTabIndex(e) {
    this.id = this.ids[e];
    this.setUrl(e);
    this.browser.scrollTop();
  }


  setUrl(index) {
    this.location.replaceState('/present/' + this.grades[index]);
    this.browser.scrollTop();
  }

  routeParse() {
    let grade = this.route.snapshot.paramMap.get('grade');
    if (grade) { grade = grade.toLowerCase(); }
    let index = this.grades.indexOf(grade);
    index = index === -1 ? 4 : index;
    this.tab = index;
    this.id = this.ids[index];
    this.setUrl(index);
  }

  ngAfterViewChecked() {
    setTimeout(() => {
      const object1 = document.querySelectorAll('.sticky') as NodeListOf<HTMLElement>;
      let h1;
      object1.forEach(e => {
        h1 = e.offsetHeight;
      });
      this.stickyTop = h1;
    });
  }

  ngOnDestroy() {
    this.navigationSubscription.unsubscribe();
  }
}
