import { Component, OnInit, OnDestroy } from '@angular/core';
import { LanguageService } from '../language.service';
import { WikiService } from '../wiki.service';
import { LevelService } from '../level.service';
import { Subscription } from 'rxjs';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { HighscoreService } from '../highscore.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-mob',
  templateUrl: './mob.component.html',
  styleUrls: ['./mob.component.css']
})
export class MobComponent implements OnInit, OnDestroy {

  navigationSubscription: Subscription;

  constructor(
    private location: Location,
    private router: Router,
    private route: ActivatedRoute,
    public lang: LanguageService,
    public wiki: WikiService
  ) { }

  id = -1;

  ngOnInit() {
    this.routeParse();
    this.navigationSubscription = this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        this.routeParse();
      }
    });
  }

  routeParse() {
    const _id = this.route.snapshot.queryParams.i;
    if (_id) {
      const g = parseInt(_id, 0);
      if (!(isNaN(g) || g < 0 || g > this.wiki.npc_base.length - 1 || this.wiki.npc_base[g].type !== 3 )) {
        this.id = g;
      } else {
        this.id = -1;
      }
    } else {
      this.id = -1;
    }
    this.setUrl(this.id);
  }

  setUrl(id: number) {
    let url = '/mob';
    url += this.id === -1 ? '' : '?i=' + id;
    this.location.replaceState(url);
  }

  ngOnDestroy() {
    this.navigationSubscription.unsubscribe();
  }
}
