import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';

import { MainComponent } from './_Out-frame/main/main.component';
import { PageNotFoundComponent } from './_Out-frame/page-not-found/page-not-found.component';
import { ItemComponent } from './item/item.component';
import { NpcComponent } from './npc/npc.component';
import { SpellComponent } from './spell/spell.component';
import { ArrowComponent } from './arrow/arrow.component';
import { MobComponent } from './mob/mob.component';
import { PetComponent } from './pet/pet.component';
import { ClComponent } from './cl/cl.component';
import { PartyQuestComponent } from './_Quests/party-quest/party-quest.component';
import { KillQuestComponent } from './_Quests/kill-quest/kill-quest.component';
import { SkillQuestComponent } from './_Quests/skill-quest/skill-quest.component';
import { DevLangComponent } from './dev-lang/dev-lang.component';
import { LootRetaliationChestComponent } from './_Loot/RetaliationChest/loot-retaliation-chest.component';
import { LootEastereggsComponent } from './_Loot/EasterEggs/loot-eastereggs.component';
import { LootSunkentreasureComponent } from './_Loot/SunkenTreasure/loot-sunkentreasure.component';
import { LootDeathdealComponent } from './_Loot/DeathDeal/loot-deathdeal.component';
import { LootPresentsComponent } from './_Loot/Presents/loot-presents.component';
import { LootHuntingRewardComponent } from './_Loot/HuntingReward/loot-hunting-reward.component';
import { RoseChestComponent } from './_Loot/rose-chest/rose-chest.component';
import { ValentinesPresentComponent } from './_Loot/valentines-present/valentines-present.component';



const routes: Routes = [
  { path: '', component: MainComponent },
  { path: 'devlang', component: DevLangComponent },
  /* 舊網址 start */
  { path: 'retaliationchest', redirectTo: 'retaliation-chest' },
  { path: 'huntingrewardchest', redirectTo: 'hunting-reward' },
  { path: 'presents', redirectTo: 'present' },
  { path: 'eastereggs', redirectTo: 'easter-egg' },
  { path: 'deathsdeal', redirectTo: 'deaths-deal' },
  { path: 'sunkentreasure', redirectTo: 'sunken-treasure' },
  { path: 'partyquest', redirectTo: 'party-quest' },
  { path: 'pq', redirectTo: 'party-quest' },
  { path: 'skillquest', redirectTo: 'skill-quest' },
  { path: 'sq', redirectTo: 'skill-quest' },
  { path: 'killquest', redirectTo: 'kill-quest' },
  { path: 'kq', redirectTo: 'kill-quest' },
  {
    path: 'boss-retaliation', children: [
      { path: '', component: LootRetaliationChestComponent },
      { path: ':grade', component: LootRetaliationChestComponent },
    ]
  },
  {
    path: 'valentines-present', children: [
      { path: '', component: ValentinesPresentComponent },
      { path: ':grade', component: ValentinesPresentComponent },
    ]
  },
  {
    path: 'rose-chest', children: [
      { path: '', component: RoseChestComponent },
      { path: ':grade', component: RoseChestComponent },
    ]
  },
  {
    path: 'hunting-reward', children: [
      { path: '', component: LootHuntingRewardComponent },
      { path: ':map', component: LootHuntingRewardComponent },
    ]
  },
  {
    path: 'present', children: [
      { path: '', component: LootPresentsComponent },
      { path: ':grade', component: LootPresentsComponent },
    ]
  },
  {
    path: 'easter-egg', children: [
      { path: '', component: LootEastereggsComponent },
      { path: ':grade', component: LootEastereggsComponent },
    ]
  },
  { path: 'deaths-deal', component: LootDeathdealComponent },
  { path: 'sunken-treasure', component: LootSunkentreasureComponent },
  {
    path: 'item',
    children: [
      { path: '', component: ItemComponent },
      { path: ':id', component: ItemComponent }
    ]
  },
  {
    path: 'npc',
    children: [
      { path: '', component: NpcComponent },
      { path: ':id', component: NpcComponent }
    ]
  },
  { path: 'spell', component: SpellComponent },
  { path: 'arrow', component: ArrowComponent },
  {
    path: 'mob', runGuardsAndResolvers: 'always',
    children: [
      { path: '', component: MobComponent },
      { path: ':id', component: MobComponent }
    ]
  },
  { path: 'pet', component: PetComponent },
  { path: 'cl', component: ClComponent },
  { path: 'party-quest', runGuardsAndResolvers: 'always', component: PartyQuestComponent },
  { path: 'kill-quest', component: KillQuestComponent },
  { path: 'skill-quest', runGuardsAndResolvers: 'always', component: SkillQuestComponent },
  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      useHash: true,
      onSameUrlNavigation: 'reload',
      preloadingStrategy: PreloadAllModules,
      scrollPositionRestoration: 'enabled'
    })
  ],
  exports: [RouterModule],
  providers: []
})
export class AppRoutingModule { }
