import { Component, OnInit, AfterViewChecked, OnDestroy } from '@angular/core';
import { LanguageService } from '../../language.service';
import { WikiService } from '../../wiki.service';
import { BrowserService } from '../../browser.service';
import { Location } from '@angular/common';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-loot-hunting-reward',
  templateUrl: './loot-hunting-reward.component.html',
  styleUrls: ['./loot-hunting-reward.component.css'],
  host: {'class': 'route-app'}
})

export class LootHuntingRewardComponent implements OnInit, AfterViewChecked, OnDestroy {

  readonly ids = [
    3411, 3439, 3442, 3421, 3440, 3412, 3445, 3424, 3425,
    3431, 3436, 3422, 3432, 3438, 3423, 3437, 3441, 3430,
    3433, 3427, 3446, 3428, 3434, 3443, 3435, 3426, 3429, 3444];
  id = '3427';
  object_keys = [];
  stickyTop;
  navigationSubscription: Subscription;

  constructor(
    public lang: LanguageService,
    public wiki: WikiService,
    private browser: BrowserService,
    private location: Location,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {

    this.ids.forEach(e => {
      this.object_keys.push({
        id: e,
        name: this.wiki.item_base[e].name.replace('Rare Key [', '').replace(']', '')
      });
    });

    this.object_keys = this.object_keys.sort((t, e) => {
      return t.name > e.name ? 1 : -1;
    });

    this.navigationSubscription = this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        this.routeParse();
      }
    });

    this.routeParse();
  }

  setTabIndex() {
    const obj = this.object_keys.filter((e, i) => e.id.toString() === this.id);
    this.setUrl(obj.length > 0 ? obj[0] : this.object_keys[Math.floor(this.object_keys.length / 2)]);
    this.browser.scrollTop();
  }

  setUrl(obj) {
    this.location.replaceState('/hunting-reward/' + obj.name.toLowerCase());
    this.id = obj.id.toString();
    this.browser.scrollTop();
  }

  routeParse() {
    let map = this.route.snapshot.paramMap.get('map');
    if (map) { map = map.toLowerCase(); }
    const obj = this.object_keys.filter((e, i) => e.name.toLowerCase() === map);
    this.setUrl(obj.length > 0 ? obj[0] : this.object_keys[Math.floor(this.object_keys.length / 2)]);
  }

  ngAfterViewChecked() {
    setTimeout(() => {
      const object1 = document.querySelectorAll('.sticky') as NodeListOf<HTMLElement>;
      let h1;
      object1.forEach(e => {
        h1 = e.offsetHeight;
      });
      this.stickyTop = h1;
    });
  }

  ngOnDestroy() {
    this.navigationSubscription.unsubscribe();
  }
}
