import { ChangeDetectorRef, Component, OnInit, Inject, ViewChild, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MediaMatcher } from '@angular/cdk/layout';
import { BrowserService } from './browser.service';
import { Router, NavigationStart } from '@angular/router';
import { MatDrawer } from '@angular/material/sidenav';
import { WikiService } from './wiki.service';

@Component({
  selector: 'app-dialog-normal',
  template: '<h1 mat-dialog-title  style="text-align: left;font-size: 14px;user-select: text;" [innerHTML]="data"></h1><div mat-dialog-actions style="float:right"><button mat-raised-button [mat-dialog-close]="data" cdkFocusInitial>Ok</button></div>',
})
export class DialogComponent {

  constructor(
    public dialogRef: MatDialogRef<DialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: string
  ) { }

  onNoClick(): void {
    this.dialogRef.close();
  }
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {

  @ViewChild('sideNavMenu') sideNavMenu: MatDrawer;
  @ViewChild('sideNavUser') sideNavUser: MatDrawer;

  private mobileQueryListener: () => void;
  mobileQuery: MediaQueryList;

  constructor(
    private dialog: MatDialog,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private browser: BrowserService,
    readonly router: Router,
    private wiki: WikiService
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this.mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this.mobileQueryListener);
  }

  ngOnInit() {
    this.router.events.subscribe((ev) => {
      if (ev instanceof NavigationStart) {
        this.browser.scrollTop();
        // when platform mobile, close sidenaves
        if (this.mobileQuery.matches) {
          this.sideNavMenu.close();
          this.sideNavUser.close();
        }
      }
    });
  }

  showDialog(msg, width = 250) {
    this.dialog.open(DialogComponent, {
      width: width + 'px',
      data: msg
    });
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this.mobileQueryListener);
  }
}
