import { Injectable } from '@angular/core';
import { BaseApiService } from './baseapi.service';
import { LevelService, Player } from './level.service';
import { BehaviorSubject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class HighscoreService {
  mainStats = new BehaviorSubject<Player>(new Player(this.level));
  mainStatsCopy: Player;
  searchCd = 0;
  searchTimer = null;
  searchPlayerName = '';
  searchStatus = new BehaviorSubject<string>('complete');

  constructor(private api: BaseApiService, private level: LevelService) {
    this.mainStatsCopy = this.mainStats.value;
    if (localStorage.loadplayer) {
      this.searchPlayerName = localStorage.loadplayer;
      this.searchPlayer();
    }
  }

  getPlayer() {
    return this.api.getPlayer(this.searchPlayerName);
  }

  searchPlayer(): void {
    if (this.searchCd !== 0) { return; }
    this.searchStatus.next('start');

    if (this.searchTimer != null) { clearInterval(this.searchTimer); }
    if (undefined === this.searchPlayerName || '' === this.searchPlayerName) { this.startTimer(1); return; }

    this.getPlayer()
      .subscribe(
        (data: any) => {
          this.startTimer(3);
          if (data.length === 0) {
            this.searchStatus.next('none');
            return;
          }
          this.searchStatus.next('success');
          const player = new Player(this.level);
          for (const i in data) {
            if (data.hasOwnProperty(i)) {
              this.mainStatsCopy[data[i].board] = player[data[i].board] = data[i].score;
            }
          }
          player.check();
          this.mainStatsCopy.name = player.name = this.searchPlayerName;
          this.mainStats.next(player);
          localStorage.loadplayer = this.searchPlayerName;
          this.searchPlayerName = '';
        },
        error => {
          this.searchStatus.next('error');
          this.startTimer(1);
        }
      );
  }

  startTimer(sec): void {
    this.searchStatus.next('cding');
    document.getElementById('btn-search').classList.add('cding');
    this.searchCd = sec;
    this.searchTimer = setInterval(cb => this.timerCountDown(), 1e3);
  }

  timerCountDown(): void {
    if (this.searchCd-- <= 1) {
      this.searchStatus.next('finished');
      clearInterval(this.searchTimer);
      this.searchCd = 0;
      return;
    }
  }
}
